.monthButtons {
  padding-bottom: 12px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.monthButtons button {
  margin: 5px !important;
  display: inline !important;
  background-color: rgba(5, 190, 192, 0.08) !important;
  box-sizing: border-box;
  border-radius: 1000px;
  text-align: center;
  color: #323232;
  border-width: 0px;
  box-shadow: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;
}

.monthButtons button:active,
.monthButtons button:global(.active) {
  background-color: #05BEC0 !important;
  color: #FFFFFF;
}

.monthButtons button.disabled {
  background-color: #4a505052 !important;
}
