.TypeSelectType {
  min-width: 95px !important;
  max-width: 95px;
  min-height: 40px;
  text-align: center;
}

.open {

}

.closed {

}


.TypeSelectType > :global(.MuiInputBase-root) {
  line-height: 0px;
}

.TypeSelectType.closed .halfWhite {
  display: none;
}

.TypeSelectType.open .halfWhite {
  display: block;
  background-color: white;
  height: 33px;
  width: 100%;
  position: absolute;
  bottom: -1px;
}

.TypeSelectType :global(.MuiSelect-root),
.TypeSelectType > :global(.MuiInputBase-root) a,
.TypeSelectType :global(.MuiSelect-root):focus {
  background: none;
  min-height: 0;
  z-index: 2;
  color: #05BEC0;
  border-color: #05BEC0;
}

.menu > :global(.MuiPaper-root) {
  box-shadow: none !important;
  margin-top: 38px;
}

.menu li.menuItem {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 1000px;
  max-width: 95px;
  padding: 0;
}

.menu li.menuItem a {
  display: list-item;
  text-align: center;
  width: 100%;
  list-style-type: none;
  color: black;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 18px;
  line-height: 24px;
}

.menu li.menuItem:global(.Mui-selected) {
  background: rgba(5, 190, 192, 0.08);
  padding-top: 10px;
  padding-bottom: 10px;
  display: none;
}


.TypeSelectType fieldset {
  border-radius: 30px;
  background-color: #FFFFFF !important;
  z-index: 1;
  color: #05BEC0 !important;
  border-color: #05BEC0 !important;
}

.TypeSelectType span,
.TypeSelectType svg {
  z-index: 2;
  color: #05BEC0;
  border-color: #05BEC0;
}

.TypeSelectType legend {
  display: none;
}

.TypeSelectType .icon {
  position: absolute;
  left: 16px;
  cursor: pointer;
}


.TypeSelectType.closed .closedIcon {
  display: none;
}

.TypeSelectType.open .openIcon {
  display: none;
}
