.SearchInput {
  margin-right: 25px !important;
  color: #DBDBDB;
}

.SearchInput label {
  margin-right: 25px;
  color: #DBDBDB;
}

.SearchInput label:global(.Mui-focused) {
  margin-right: 0;
}

.SearchInput :global(.MuiInputBase-root) {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: nowrap;
}

.SearchInput :global(.MuiInputBase-root):before {
  border-bottom: 1px solid #DBDBDB;
}

.SearchInput svg {
  color: #DBDBDB;
}

.SearchInput.maxWidth {
  width: 420px;
}
