.searchForm {
  display: contents;

}


.searchInputsContainer {
  min-height: 43px;
  border-radius: 24px;
  padding-left: 23px;
  padding-right: 0px;
  padding-top: 5px;
  width: fit-content;
  width: -moz-max-content;
  margin-left: auto;
  margin-right: auto;
}

.searchInputsContainer .searchButtonsRow {
  width: 716px;
  background-color: white;
  padding-left: 22px;
  padding-right: 22px;
  padding-top: 6px;
  padding-bottom: 2px;
  border-radius: 25px;
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.searchInputsContainer .searchSelectorsRow {
  width: 716px;
  background-color: white;
  border-radius: 32px;
  margin-top: 12px !important;
  padding-left: 20px;
  display: flex;
  flex-wrap: nowrap;
  position: absolute;
  z-index: 1;
  display: flex;
  margin-right: -15px;
  margin-left: -15px;
}


.searchInputsContainer .searchTypeLabel {
  font-weight: 500;
  font-size: 18px;
  line-height: 33px;
  text-align: right;
  color: #323232 !important;
  padding-left: 16px;
  padding-right: 16px;
}

.searchInputsContainer svg {
  margin-left: 8px;
  margin-top: -2px;
  color: #878787;

}

.searchInputsContainer svg.expand {
  margin-left: 8px;
  margin-top: -2px;
  color: #878787;
}

.expandingBox {
  flex-grow: 1;
  padding-right: 20px;
  z-index: 200;
}


.actionsBox {
  flex: 0 0 72px;
}


.lowResSearchButton {
  display: none !important;
}

@media (max-width: 769px) {
  .searchInputsContainer {
    padding-left: 0px;
    background-color: transparent;
    width: 100%;
  }

  .searchInputsContainer .searchButtonsRow .highResSearchButton {
    display: none;
  }
  .lowResSearchButton {
    display: block !important;
    width: 88% !important;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15px !important;
    margin-bottom: 25px !important;

  }

  .searchInputsContainer .searchButtonsRow {
    background-color: transparent;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }


  .searchInputsContainer .searchSelectorsRow {
    display: none;
    height: fit-content;
    overflow: initial;
    width: 88%;
    background-color: white;
    z-index: 1;
    /* padding: 6px 22px 2px; */
    border-radius: 25px;
    margin-left: auto;
    margin-right: auto;
    clear: both;
    padding: 0;

    position: relative;
    top: -15px;
    padding-bottom: 25px;
  }

  .searchInputsContainer.expanded .searchSelectorsRow {
    display: block;
  }

  .searchInputsContainer :global(.MuiSvgIcon-root.expand) {
    float: left;
    margin-top: 3px;
  }

  .expanded .expandingBox {
    width: 100%;
    height: fit-content;
    min-height: 150px;
    padding: 15px;
  }
