.yearSelector {
  height: 48px;
}

.yearSelector :global(.MuiFormControl-root .SearchSelector__control) {
  padding: 0;
  padding-right: 8%;
  z-index: 556;
  border-color: transparent !important;
  border-radius: 25px;
  width: 123px;
  height: 40px;
  top: 0px !important;
  background-color: rgba(5, 190, 192, 0.08) !important;
  color: #323232 !important;
  font-weight: 500 !important;
  font-size: 18px !important;
  line-height: 24px !important;
}


.yearSelector :global(.MuiFormControl-root .SearchSelector__control .SearchSelector__placeholder) {
  color: #05BEC0 !important;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-align: right;
  margin-right: auto;
  margin-left: auto;
}


.yearSelector :global(.MuiFormControl-root .SearchSelector__control--menu-is-open) {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom: transparent;
}

.yearSelector :global(.SearchSelectorContainer .MuiFormControl-root .SearchSelector__menu) {
  z-index: 80001;
  border-width: 1px;
  border-style: solid;
  background-color: rgb(218, 248, 249) !important;
  border-radius: 52px !important;
}


.yearSelector :global(div.SearchSelector__menu) {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-top: -1px !important;
  z-index: 55555;
  background-color: rgb(218, 248, 249);
  border-color: transparent;
  box-shadow: none !important;
}


.yearSelector :global(div.SearchSelector__menu .SearchSelector__menu-list) {
  max-height: 125px !important;
}

.yearSelector :global(.MuiFormControl-root .SearchSelector__control.valid) {
  border-color: transparent !important;
}

.yearSelector :global(div.SearchSelector__menu div.SearchSelector__option) {
  background-color: transparent !important;

  font-weight: 500 !important;
  font-size: 18px !important;
  line-height: 24px !important;
  /* identical to box height, or 133% */
  z-index: 55555;
  text-align: right;
  color: #323232 !important;

}

.yearSelector :global(div.SearchSelector__menu div.SearchSelector__option:hover) {
  background-color: #dddada !important;
}

.yearSelector :global(div.SearchSelector__menu div.SearchSelector__option.last:hover) {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}


.yearSelector :global(div.SearchSelector__value-container.SearchSelector__value-container--has-value  div.SearchSelector__single-value) {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #323232 !important;
  margin-right: 10px;
}


.yearSelector :global(.SearchSelector__option--is-selected) {
  display: none;
}

.yearSelector .dropDownArrow {
  position: absolute;
  left: 10px;
  color: #323232 !important;
}

.yearSelector :global(.SearchSelector__indicator-separator) {
  display: none !important;
}


.yearSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar {
  width: 3px !important; /* width of the entire scrollbar */
}

.yearSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track {
}

.yearSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-thumb {
  background-color: #05BEC0 !important; /* color of the scroll thumb */
  border-radius: 20px !important; /* roundness of the scroll thumb */
}

.yearSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track-piece:end {
  background-color: transparent !important;
  margin-bottom: 15px !important;
}

.yearSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track-piece:start {
  background-color: transparent !important;
  margin-top: 15px !important;
}
