.actionButtonsContainer {
  width: fit-content;
  margin-right: auto;
  margin-left: -13px;
  position: absolute;
  bottom: 5px;
  left: 25px;
  z-index: 200;
}

.actionButtonsContainer .actionButtons {
  display: inline-block;
  outline: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #05BEC0 !important;
  border-color: #05BEC0 !important;
  margin-bottom: 0;
  box-shadow: none !important;
  border-style: solid;
  border-radius: 52px;
  border-color: #BEBEBE;
}

.actionButtonsContainer .saveButton {
  min-width: 60px;
  margin-bottom: 0;
  padding: 3px;
  z-index: 51;
}

