.actionButtonsContainer .saveButton:focus,
.actionButtonsContainer .saveDisketIcon:focus, {
  outline: none;
}

@media (max-width: 769px) {
  .actionButtonsContainer {
    position: initial;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

