.rightSideBorder {
  margin-right: auto;
  margin-left: -20px;
  margin-top: -2px;
  max-height: 41px;
}

.rightSideBorder .searchButtons {
  border-radius: 52px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #F28F17;
  min-width: 100px;
  margin-bottom: 4px;
}

.rightSideBorder .searchButtonsLeft {
  display: inline-block;
  min-width: 50px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  margin-bottom: 0;
}

.rightSideBorder .searchButtonsRight {
  display: inline-block;
  min-width: 50px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  margin-bottom: 0;
  color: #F28F17;
  background-color: #ffffff;
  border-style: solid;
  border-width: 1px;
  padding-top: 7px;
  padding-bottom: 7px;
}

@media (max-width: 769px) {
  .rightSideBorder {
    display: block;
    width: 100%;
    //  margin-left: 20px;
    // margin-right: 20px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .rightSideBorder .searchButtons {
    width: 100%;
  }

  .rightSideBorder .searchButtonsLeft,
  .rightSideBorder .searchButtonsRight {
    width: 50%;
  }

  :global(.expanded) .rightSideBorder {
    position: relative;
    z-index: 555;
  }
