.querySelectorsContainer :global(.MuiChip-root.MuiChip-deletable) {
  background-color: white;
  color: black;
}

.querySelectorsContainer {
  max-width: 716px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  position: relative;
  right: -9px;
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 769px) {
  .querySelectorsContainer {
    right: 0;
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;
  }
}
