.whenTab {
  display: flex;
  flex-wrap: nowrap;
  margin-right: -15px;
  margin-left: -15px;
  flex-direction: row;
  justify-content: space-around;
  padding: 10px;
}


@media (max-width: 400px) {
  .whenTab {
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
  }
}
