.continentButtons button {
  margin: 5px !important;
  display: inline !important;
  background-color: rgba(5, 190, 192, 0.08) !important;
  padding: 8px 8px 8px 8px;
  box-sizing: border-box;
  border-radius: 1000px;
  text-align: center;
  color: #323232;
  border-width: 0px;
  box-shadow: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;

}

.continentButtons :global(button:active),
.continentButtons :global(button.active) {
  background-color: #05BEC0 !important;
  color: #FFFFFF;
}

.continentButtons :global(button.disabled) {
  background-color: #4a505052 !important;
}
