.typeResultsContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  max-width: 1140px;
  width: 100%;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 100px;
}


@media (max-width: 1281px) {
  .typeResultsContainer {
    padding-left: 25px;
    padding-right: 25px;
  }
}

@media (max-width: 392px) {
  .typeResultsContainer {
    padding-left: 5px;
    padding-right: 5px;
  }
}

