.TabButton {
  border-style: solid;
  border-width: 0px;
  border-left-width: 1px;
  border-color: #DBDBDB;
  margin-bottom: 6px;
  padding-right: 15px;
  cursor: pointer;
}

.TabButton.noBorder {
  border-width: 0px;
}


.TabButton:hover svg,
.TabButton:hover .searchTypeLabel {
  // color: #05BEC0 !important;
}

.TabButton.selected svg,
.TabButton.selected .searchTypeLabel {
  color: #05BEC0 !important;
}

.searchTypeLabel {
  font-weight: 500;
  font-size: 18px;
  line-height: 33px;
  text-align: right;
  color: #323232 !important;
  padding-left: 16px;
  padding-right: 16px;
}

@media (max-width: 769px) {

  .TabButton {
    background-color: #ffffff;
    border-radius: 30px;
    display: block;
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 25px;
  }
  :global(.expanded) .TabButton {
    display: none;
  }

  :global(.expanded) .TabButton.selected {
    display: block;
  }

